"use client";

import { useState } from "react";
import { postReport } from "@/actions/postReport";
import { useSession } from "@/app/lib/auth-client";
import { useData } from "@/app/components/layout/DataProvider";
import { toast } from "sonner";

interface PopupFormProps {
  latitude: number;
  longitude: number;
  onClose: () => void;
}

const PopupForm = ({ latitude, longitude, onClose }: PopupFormProps) => {
  const { reportTypes } = useData();
  const [selectedReportType, setSelectedReportType] = useState<number>(
    reportTypes.length > 0
      ? [...reportTypes].sort((a, b) => a.id - b.id)[0].id
      : 0
  );
  const [formData, setFormData] = useState("");
  const { data: session } = useSession();
  const [loading, setLoading] = useState(false);

  const userId = session?.user?.id || "";
  const trustScore = session?.user?.trust || 0;

  const selectedReport = reportTypes.find(
    (type) => type.id === selectedReportType
  );

  const handleInputChange = (field: string, value: string) => {
    setFormData((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoading(true);

    try {
      const payload = {
        latitude,
        longitude,
        selectedReportType,
        trustScore,
        userId,
        description: formData,
      };

      const response = await postReport({ payload });

      if (!response) {
        throw new Error("Failed to submit report");
      }

      toast.success("Report submitted successfully!");
      setFormData("");
      onClose();
    } catch (error) {
      toast.error("Failed to submit report. Please try again.");
      console.error("❌ Failed to submit report:", error);
    } finally {
      setLoading(false);
    }
  };

  const roundedLatitude = Number(latitude).toFixed(4);
  const roundedLongitude = Number(longitude).toFixed(4);

  return (
    <div className="">
      <form onSubmit={handleSubmit} className="p-2">
        <div>
          <label htmlFor="reportType" className="block font-bold">
            Report Type:
          </label>
          <select
            id="reportType"
            value={selectedReportType}
            onChange={(e) => setSelectedReportType(Number(e.target.value))}
            required
            className="border rounded-sm p-1 w-full"
          >
            {reportTypes
              .slice()
              .sort((a, b) => a.id - b.id)
              .map((type) => (
                <option key={type.id} value={type.id}>
                  {type.name}
                </option>
              ))}
          </select>
        </div>

        {/* Dynamic Fields Section */}
        {selectedReport?.fields?.map((field) => (
          <div className="mt-2" key={field}>
            <label className="block font-bold">{field}:</label>
            <input
              type="text"
              value={formData[field] || ""}
              onChange={(e) => handleInputChange(field, e.target.value)}
              className="border rounded-sm p-1 w-full"
            />
          </div>
        ))}

        <div className="mt-2">
          <label htmlFor="description" className="block font-bold">
            Description:
          </label>
          <textarea
            id="description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            className="border rounded-sm p-1 w-full"
          />
        </div>
        <div className="mt-2">
          <p>
            <strong>Latitude:</strong> {roundedLatitude}
          </p>
          <p>
            <strong>Longitude:</strong> {roundedLongitude}
          </p>
        </div>
        <div className="mt-2 flex space-x-2">
          <button
            type="submit"
            className="bg-blue-500 text-white px-3 py-1 rounded-sm hover:cursor-pointer"
            disabled={loading}
          >
            {loading ? (
              <span className="flex items-center">
                <svg
                  className="animate-spin h-4 w-4 mr-2 text-white"
                  viewBox="0 0 24 24"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <circle
                    className="opacity-25"
                    cx="12"
                    cy="12"
                    r="10"
                    stroke="currentColor"
                    strokeWidth="4"
                  ></circle>
                  <path
                    className="opacity-75"
                    fill="currentColor"
                    d="M4 12a8 8 0 018-8v8H4z"
                  ></path>
                </svg>
                Submitting...
              </span>
            ) : (
              "Submit Report"
            )}
          </button>
          <button
            type="button"
            className="bg-gray-500 text-white px-3 py-1 rounded-sm hover:cursor-pointer"
            onClick={onClose}
            disabled={loading}
          >
            Cancel
          </button>
        </div>
      </form>
    </div>
  );
};

export default PopupForm;
